.App {
  display: flex;
}
.sidebar {
  width: 300px;
}

.sideBar-profile {
  background-color: rgb(245, 245, 245);
}

.sideBar-chats {
  border-style: solid;
  border-width: 1px;
  margin-right: 5px;
}

.sideBar-search {
  margin-left: 10px;
}

.sidebar-chats h1 {
  margin-left: 10px;
}

.users {
  overflow: auto;
  width: 250px;
}

.user {
  border-bottom: solid;
  border-width: 1px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.contact-dialog {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 120px;
}

.contact-dialog h1 {
  margin-left: 10px;
}

.list-dialog {
  border-style: solid;
  height: 500px;
  border-width: 1px;
  width: 1000px;
}

.accepted-profile {
  display: flex;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 10px;
}

.accepted-messages {
  display: inline-block;
  background-color: #000;
  border-radius: 20px;
}

.accepted-messages p {
  color: #ffff;
}

.send {
  display: flex;
  justify-content: flex-end;
}

.send-messages {
  display: inline-block;
  background-color: #c0c0c0;
  border-radius: 20px;
}

.chat-search {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

.chat-search input {
  width: 800px;
  height: 50px;
  border-radius: 30px;
  padding-left: 10px;
}

.chat-search button {
  width: 100px;
  border-radius: 15px;
  margin-left: 20px;
  background-color: black;
  color: #c0c0c0c0;
}

.advise-list {
  display: flex;
  justify-content: center;
  padding-top: 150px;
}
